.footer__social {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    position: relative;
}
.footer__social li {
    display: block;
    margin-right: 25px;
}

  .footer-input{
    display: block;
    background: rgba(255,255,255,0);
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 25px;
    padding: 0 60px 0 25px;
    margin: 0;
    font-size: 16px;
    color: #fff;
    font-weight: 400;
    letter-spacing: 0.2px;
  }

  .subscribe{
    /* display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative; */
    margin: 30px auto 0;
    /* width: 100%; */
    max-width: 360px;
    background: linear-gradient(90deg, rgba(100,125,238,0.15) 0%, rgba(167,84,226,0.15) 100%);
    border-radius: 25px;
    /* overflow: hidden; */
    text-align: center;
    border: transparent;
  }

  .subscribe:focus{
    outline: none;
  }

  .social-bar:hover{
    color: #20284d !important;
    cursor: pointer;
  }