.progress {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
    height: 40px;
    background-color: #fff;
    border-radius: 20px;
    margin-bottom: 30px;
    margin-top: 50px;
    max-width: 500px;
    position: relative;
}


.progress-bar 
{
        display: flex;
        flex-direction: column;
        justify-content: center;
        overflow: hidden;
        text-align: center;
        white-space: nowrap;
        background-color: #647dee;
        border-radius: 20px;
        transition: width 0.6s ease;
        background-image: linear-gradient(-45deg, rgba(0,0,0,0.15) 25%, transparent 25%, transparent 50%, rgba(0,0,0,0.15) 50%, rgba(0,0,0,0.15) 75%, transparent 75%, transparent);
        background-size: 100px 100px;
        animation: progress-bar-stripes 3s linear infinite;
        width: 20%;
    
}

.progress__line {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    pointer-events: none;
    top: -30px;
    bottom: 0;
    z-index: 22;
    font-size: 14px;
    color: #fff;
}

.progress__line:before {
    content: '';
    position: absolute;
    display: block;
    bottom: 0;
    left: 0;
    top: 25px;
    background-color: rgba(255,255,255,0.15);
    width: 1px;
    border-radius: 2px;
}

