.about__text {
    display: block;
    background-color: #fff;
    position: relative;
    padding: 20px;
    border-radius: 20px;
    z-index: 4;
    word-break: break-all;
}

.about__text:before, .about__text:after {
    content: '';
    position: absolute;
    display: block;
    opacity: 0.2;
    background-image: -moz-linear-gradient(-60deg, #647dee 0%, #a754e2 100%);
    background-image: -webkit-linear-gradient(-60deg, #647dee 0%, #a754e2 100%);
    background-image: -ms-linear-gradient(-60deg, #647dee 0%, #a754e2 100%);
    background-image: linear-gradient(-60deg, #647dee 0%, #a754e2 100%);
    border-radius: 0 0 10px 10px;
    transition: 0.4s ease-in-out;
}

.about__text:after {
    z-index: 1;
    left: 30px;
    right: 30px;
    height: 20px;
    bottom: -20px;
}

.about__text:before {
    z-index: 2;
    left: 20px;
    right: 20px;
    height: 10px;
    bottom: -10px;
}

.section--gradient {
    background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
}

.about{
    display: block;
    position: relative;
}

.about__text:before {
    z-index: 2;
    left: 20px;
    right: 20px;
    height: 10px;
    bottom: -10px;
}

.about__text:after {
    z-index: 1;
    left: 30px;
    right: 30px;
    height: 20px;
    bottom: -20px;
    content: '';
    position: absolute;
    display: block;
    opacity: 0.2;
    background-image: linear-gradient(-60deg, #647dee 0%, #a754e2 100%);
    border-radius: 0 0 10px 10px;
    transition: 0.4s ease-in-out;

}

.buycoin-p{
    font-family: 'Jura', sans-serif;
    font-weight: 600;
    display: block;
    text-align: center;
    color: white;
    margin: 2rem;
}

.about-header{
    text-align: center;
    font-family: 'Big Shoulders Stencil Display', cursive;
    color: black;
}