@import 'https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0-alpha.5/css/bootstrap.min.css';

.navbar-fixed-top.scrolled {
  background-color: #fff !important;
  transition: background-color 200ms linear;
  box-shadow: 2px 0 9px rgba(0,0,0,0.26);
}

.navbar-fixed-top.scrolled .nav-link {
  color: #555;
  
}

.home_text {
  text-align: center;
  color: white;
  transition: all 4s;
}

.bg-primary{
  background-color: transparent !important;
}

.navbar-btn{
  background: linear-gradient(90deg, #a754e2 0%, #647dee 100%);
  border-radius: 22px;
  border: none;
}


.home_content{
  z-index: 33;
}

.progress{
  z-index: 33;
}

.home_btns{
  z-index: 33;
}

.logo-view{
  display: none;
}

@media screen and (max-width: 768px) {
  .line1{
    width: 35px;
    height: 3px;
    background-color: white;
    margin: 5px;
    list-style: none;
  }
  .line2{
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 5px;
    list-style: none;
  }
  .line3{
    width: 15px;
    height: 3px;
    background-color: white;
    margin: 5px;
    list-style: none;
  }
  header {
    position: relative;
  }
  .hamburger {
    position: absolute;
    cursor: pointer;
    top: 5%;
    z-index: 10933332;
    transform: translate(-5%, -50%);
  }
  .logo-view {
    position: absolute;
    cursor: pointer;
    right: 42%;
    top: 5%;
    z-index: 10933332;
    transform: translate(-5%, -50%);
    display: block;
  }

  .open img{
    display: none;
  }
  .top-navbar {
    position: fixed;
    background: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
    height: 100vh;
    width: 100%;
    z-index: 44532;
    flex-direction: column;
    clip-path: circle(100px at 90% -10%);
    -webkit-clip-path: circle(100px at 90% -20%);
    transition: all 1s ease-out;
    display: inline-grid;
    justify-content: center;
  }
  .top-navbar.open {
    clip-path: circle(1000px at 90% -10%);
    -webkit-clip-path: circle(1000px at 90% -10%);
    display:grid;
    justify-content: center;
  }

  .navbar-nav{
    display: grid;
    text-align: center;
    justify-content: normal;
    margin: 3rem;
  }

  .navbar-btn-signin{
    display: grid;
  }
}

.nav-link:hover{
  color: #009ACD !important;
}

.navbar-dark .navbar-nav .nav-link {
  color:white;
}

.scrolled .nav-link{
  color: black !important;
}