.section__tagline {
  display: block;
  position: relative;
  text-align: center;
  font-size: 18px;
  font-family: 'Jura', sans-serif;
  font-weight: 600;
  color: #647dee;
  margin-bottom: 30px;
  margin-top: 2rem;
}

.section__tagline:before {
  content: '';
  position: absolute;
  display: block;
  width: 60px;
  height: 3px;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 3px;
  background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
}

.section__title {
  font-size: 32px;
  color: #343434;
  font-family: 'Jura', sans-serif;
  font-weight: 600;
  display: block;
  text-align: center;
  margin-bottom: 5px;
}

.roadmap {
  position: relative;
  display: block;
  margin-top: 40px;
  padding: 25px 0;
}

.roadmap:before {
  content: '';
  position: absolute;
  display: block;
  width: 4px;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 10px;
  border-radius: 4px;
  background-image: linear-gradient(180deg, #647dee 0%, #a754e2 100%);
}

.roadmap li:before {
  width: 24px;
  height: 24px;
  left: 0;
  background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
  opacity: 0.37;
  z-index: 1;
  top: 1px;
}

.roadmap li.active:after {
  background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
}

.roadmap li:after {
  width: 16px;
  height: 16px;
  left: 4px;
  top: 5px;
  background-color: #fff;
  z-index: 2;
}

.roadmap li:before,
.roadmap li:after {
  content: '';
  position: absolute;
  display: block;
  border-radius: 50%;
}

@media (min-width: 1200px) {
  .roadmap li span {
    margin-bottom: 60px;
  }

  .roadmap li {
    display: block;
    max-width: 210px;
    padding-left: 0;
    margin: 0 15px;
  }

  .roadmap li:after {
    top: 46px;
  }

  .roadmap li:before {
    top: 42px !important;
  }

  .roadmap:before {
    width: auto;
    height: 4px;
    top: 52px;
    left: 0;
    right: 0;
    background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
  }

  .offset-xl-0 {
    margin-left: 0;
  }

  .col-xl-12 {
    float: left;
    width: 100%;
    /* -ms-flex: 0 0 100%; */
    flex: 0 0 100%;
    max-width: 100%;
  }

  .roadmap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding: 0;
    margin-top: 70px;
    width: 100%;
  }
}

@media (min-width: 768px) {
  .roadmap li span {
    font-size: 18px;
    /* margin-bottom: 10px; */
  }
}

.roadmap li span {
  /* font-size: 16px; */
  color: #343434;
  line-height: 24px;
  font-weight: 500;
  display: block;
  /* margin-bottom: 5px; */
}

.roadmap li p {
  font-size: 16px;
  color: #505050;
  line-height: 24px;
  margin-bottom: 0;
}

.roadmap li.active:after {
  background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
}

.roadmap li {
  position: relative !important;
  display: block;
  margin-bottom: 20px;
  padding-left: 40px;
}

.roadmap-p {
  color: white !important;
}

.roadmap-span {
  color: white !important;
}


.roadmap-footer{
    text-align: center;
    color: white;
}
