.contact-text::before{
    content: '';
    position: absolute;
    display: block;
    width: 60px;
    height: 3px;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 3px;
    background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
}

.contact-text{
    font-weight: 400;
    color: #505050;
    display: block;
    text-align: center;
    position: relative;
    margin-bottom: 30px;
    margin-top: 20px;
    color: white;
}

.contacts__list li span{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
    margin-right: 15px;
}

.contacts__list li {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
}

.form__input {
    display: block;
    background-color: #fff;
    width: 100%;
    height: 50px;
    border: 2px solid #647dee;
    border-radius: 25px;
    padding: 0 20px;
    margin-bottom: 20px;
    font-size: 16px;
    color: #343434;
}

.form__textarea {
    display: block;
    background-color: #fff;
    width: 100%;
    height: 140px;
    border: 2px solid #647dee;
    border-radius: 25px;
    padding: 15px 20px;
    margin-bottom: 20px;
    font-size: 16px;
    color: #343434;
    resize: none;
}

.form__btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 180px;
    height: 50px;
    font-size: 14px;
    font-weight: 500;
    border-radius: 25px;
    letter-spacing: 0.4px;
    color: #fff;
    text-align: center;
    background: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
    text-transform: uppercase;
    overflow: hidden;
    margin: 0 auto;
    border-color: transparent;
}

.form__btn:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, #a754e2 0%, #647dee 100%);
    opacity: 0;
    z-index: 1;
    pointer-events: none;
    transition: 0.4s ease;
}

.social-logo-color{
    color: white;
}

.contacts__list{
    display: contents;
}

.container{
    background-color: #20284d;
    color: white;
}