.tagline-number{
    line-height: 100%;
    font-weight: 500;
    margin-bottom: 15px;
    background-image: -webkit-linear-gradient(0deg, #647dee 0%, #a754e2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #647dee;
    font-size: 3rem;
}

.box4__text:before {
    content: '';
    position: absolute;
    display: block;
    width: 50px;
    height: 3px;
    bottom: -23px;
    left: 0;
    background-image: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
    border-radius: 3px;
}
.border-box{
    border: none;
    background-color: #20284d;
}

.signbtn{
    
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 180px;
        height: 50px;
        font-size: 14px;
        font-weight: 500;
        border-radius: 25px;
        letter-spacing: 0.4px;
        color: #fff;
        background: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
        text-transform: uppercase;
        margin: 40px auto 0;
        overflow: hidden;
        border: none;
    
}

#root{
    background-color: #20284d;
}