.section__tabs {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: auto;
    position: relative;
    margin: 0 auto 40px;
    background: linear-gradient(90deg, rgba(100,125,238,0.05) 0%, rgba(167,84,226,0.05) 100%);
    border-radius: 25px;
}

.section__tabs li {
    display: block;
    width: 100%;
    background-color: transparent;
}

.section__tabs li:first-child a {
    border-left: 2px solid #647dee;
    border-radius: 25px 0 0 25px;
}

.section__tabs a {
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    text-transform: uppercase;
    color: #505050;
    height: 46px;
    background-color: transparent;
    border-top: 2px solid #647dee !important;
    border-bottom: 2px solid #647dee !important;
    letter-spacing: 0.4px;
}

.offers-class{
    padding: 1rem;
    text-align: center;
}

.offer-body{
    border: none;
    background: linear-gradient(90deg, rgba(100,125,238,0.05) 0%, rgba(167,84,226,0.05) 100%);
}

.offer-col{
    background: linear-gradient(90deg, rgba(100,125,238,0.05) 0%, rgba(167,84,226,0.05) 100%);
    border: 2px solid #647dee;
    /* border-right: none;  */
}


.offer-cols{
    background: linear-gradient(90deg, rgba(100,125,238,0.05) 0%, rgba(167,84,226,0.05) 100%);
    border: 2px solid #647dee;
}

.offers__btn{
    width: 110px;
    height: 46px;
    font-size: 14px;
    font-weight: 500;
    border-radius: 23px;
    letter-spacing: 0.4px;
    color: #fff;
    text-align: center;
    background: linear-gradient(90deg, #647dee 0%, #a754e2 100%);
    text-transform: uppercase;
    border-color: transparent;
}

