.offerbody-p{
    display: block;
    font-weight: 500;
    color: white;
    font-size: 20px;
    line-height: 100%;
}

.offerbody-pp{
    display: block;
    font-weight: 500;
    color: white;
    font-size: 32px;
    line-height: 100%;
}

.section__text{
    color: white;
}